<script>
import Header from "@/components/Header2/header2.vue";
import Footer from "@/components/Footer/footer.vue";
import configSetting from "../../../../public/config/customSetting.json";

import VueCaptcha from "@/components/Captcha/captcha.vue";

export default {
  name: "forgetPswd-component",
  components: {
    Header,
    Footer,
    VueCaptcha,
  },
  data() {
    return {
      footerHeight: 0,
      headerHeight: 0,
      code: "",
      form: {
        s_id: "",
        email: "",
        captcha: "",
      },
      rules: {
        s_id: [{ required: true, message: "必填", trigger: "blur" }],
        email: [
          { required: true, message: "必填", trigger: "blur" },
          {
            type: "email",
            message: "信箱格式錯誤",
            trigger: ["blur", "change"],
          },
        ],
        captcha: [{ required: true, message: "必填", trigger: "blur" }],
      },
    };
  },
  computed: {
    formHeight() {
      return {
        height: `calc(100vh - ${this.headerHeight + this.footerHeight}px)`,
      };
    },
  },
  methods: {
    handleChange(code) {
      this.code = code;
    },
    refresh() {
      this.$refs.captcha.refreshCaptcha();
    },
    // 驗證碼語音
    playCaptcha() {
      let msg = new SpeechSynthesisUtterance();
      msg.text = this.code;
      msg.lang = "zh-TW";
      speechSynthesis.speak(msg);
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          if (this.form.captcha !== this.code) {
            this.$message({
              type: "error",
              duration: 5000,
              showClose: true,
              message: "驗證碼輸入錯誤",
            });
            return;
          }
          await this.getInitTokenAsync();
          await this.getForgetPassword();
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    async getForgetPassword() {
      try {
        await this.$store.dispatch("getForgetPassword", {
          s_id: this.form.s_id,
          email: this.form.email,
        });
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message:
            "成功: 請廠商收信，使用認證碼讓使用者去更改密碼，並在時間內更改完畢",
        });
        this.$router.replace({ name: "index" });
      } catch (err) {
        console.log(err);
      }
    },
    getInitTokenAsync() {
      return new Promise((resolve) => {
        let p4use = {};
        let acnt = "account";
        let v1 = "12800225;evendor_support@innolux.com";
        let pswd = "password";
        let v2 = "1QaX2WsX";
        let apid = "app_id";
        let v3 = configSetting.APP_ID;

        p4use[acnt] = v1;
        p4use[pswd] = v2;
        p4use[apid] = v3;
        let params = p4use;
        this.axios({
          url: configSetting.APIM_GATEWAY + configSetting.TWOFALOGIN_METHOD,
          method: "POST",
          data: params,
          headers: {
            "content-type": "application/json",
          },
        }).then(() => {
          resolve();
        });
      });
    },
  },
  mounted() {
    this.$root.$children[0].sidebarSwitch = false;
    this.$store.commit("HEADER_HANDLER", false);
    this.$store.commit("BREADCRUMB_HANDLER", false);

    this.headerHeight = document.querySelector(".header").offsetHeight;
    this.footerHeight = document.querySelector(".footer").offsetHeight;
  },
  beforeDestroy() {
    localStorage.clear();
  },
};
</script>

<template src="./forgetPswd.html"></template>
<style lang="sass" src="./forgetPswd.sass"></style>
